@import "https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@400;700&family=Merriweather:wght@400;700&family=Crimson+Text:wght@400;700&display=swap";
body {
  color: #0000;
  background-color: #d1b899;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

h1 {
  color: #362004;
  justify-content: center;
  padding-top: 5rem;
  padding-bottom: 1rem;
  font-family: Crimson, serif;
  font-size: 65px;
  font-weight: bold;
  display: flex;
}

h2 {
  color: #533612;
  justify-content: center;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: Merriweather, serif;
  font-size: 20px;
  font-weight: 600;
}

.arts-div p {
  color: #000;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  font-family: Source Sans Pro, sans-serif;
  font-size: 16px;
}

main {
  background-color: #d1b899;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem;
}

.arts-div {
  background-color: #d1b899;
  border-top: 2px solid #362004;
  gap: 1rem;
  height: auto;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
}

#arts-sidebar {
  color: #fff;
  background-color: #362004;
  flex-direction: column;
  grid-template-columns: 1fr;
  justify-content: center;
  width: 50%;
  height: auto;
  display: flex;
}

#latest-arts {
  transition: all .3 ease;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  width: 50%;
  height: auto;
  padding: 1rem;
  display: grid;
}

.art-article {
  transition: all .3 ease;
  flex-direction: column;
  align-content: baseline;
  align-items: center;
  gap: 1rem;
  height: auto;
  display: grid;
  position: relative;
}

.art-photo {
  object-fit: cover;
  filter: grayscale(75%);
  justify-content: center;
  width: 100%;
  height: 250px;
  padding: .5rem;
  display: flex;
}

.art-article:hover .art-photo {
  filter: none;
  border-radius: 25px;
  transition: all .3s;
}

.art-article:hover {
  background-color: #c29e6e;
  border-radius: 20px;
  transition: all .3s;
  transform: translateY(.5rem);
}

.title-box {
  opacity: .9;
  color: #fff;
  background-color: #a37d55;
  justify-content: center;
  width: 500px;
  height: 250px;
  padding: 1rem;
  position: absolute;
  top: 50px;
  right: 20px;
}

.art-title a {
  color: #fff;
  justify-content: left;
  padding: 1rem 0;
  font-family: Playfair Display, sans-serif;
  font-size: 40px;
  position: absolute;
}

@media (width <= 768px) {
  h1 {
    padding-top: 3rem;
    font-size: 45px;
  }

  .title-box {
    background-color: #0000;
  }

  .art-title a {
    display: none;
  }

  .art_one, .arts-photo-one {
    width: 100%;
    height: 500px;
  }

  .arts-sidebar {
    display: none;
  }

  h2 {
    padding: 0 .5rem;
    font-size: 18px;
  }

  .arts-div {
    flex-direction: column;
    padding: 1rem;
  }

  #arts-sidebar, #latest-arts {
    width: 100%;
  }

  #latest-arts {
    grid-template-columns: 1fr;
  }

  .art-photo {
    height: 200px;
  }

  .title-box {
    width: 100%;
    height: auto;
    padding: .5rem;
    top: 20px;
    right: 0;
  }

  .art-title a {
    padding: .5rem 0;
    font-size: 30px;
  }
}

@media (width <= 480px) {
  h1 {
    font-size: 35px;
  }

  h2 {
    font-size: 16px;
  }

  .arts-div {
    padding: .5rem;
  }

  .art-photo {
    height: 150px;
  }

  .title-box {
    padding: .5rem;
  }

  .art-title a {
    font-size: 24px;
  }
}
